<template>
    <div id="contact-bar" class="contact-bar">
        <div class="row" style="justify-content: space-between">
            <div class="left-side row" style="gap: 70px;">
                <div class="row language">
                    <b-dropdown dropdown>
                        <template #button-content>
                            <b-icon icon="globe" style="margin-right: 5px;"></b-icon>
                            {{ display_lang }}
                            <b-icon icon="chevron-down"></b-icon>
                        </template>
                        <b-dropdown-item @click="handleChange('ar')" v-if="lang == 'en'">العربية</b-dropdown-item>
                        <b-dropdown-item @click="handleChange('en')" v-if="lang == 'ar'">English</b-dropdown-item>
                    </b-dropdown>
                </div>
                |
                <div class="row" style="gap: 20px;" v-if="screenWidth >= 992">
                    <div class="social-icon">
                        <a href="https://www.facebook.com/peaklink.sy" target="_blank" class="icons">
                            <b-icon icon="facebook"></b-icon>
                        </a>
                    </div>
                    <div class="social-icon">
                        <a href="https://www.instagram.com/peak__link/" target="_blank" class="icons">
                            <b-icon icon="instagram"></b-icon>
                        </a>
                    </div>
                    <div class="social-icon">
                        <a href="https://www.linkedin.com/company/peak-link/" target="_blank" class="icons">
                            <b-icon icon="linkedin"></b-icon>
                        </a>
                    </div>
                </div>
            </div>
            <a class="row icons" style="gap: 10px;margin-right: 35px" href="https://wa.me/00963989202089" target="_blank"
                v-if="screenWidth < 992">
                <p>+963 989 202 089</p>
                <b-icon icon="whatsapp"></b-icon>
            </a>
            <div class="right-side row" v-if="screenWidth >= 992">
                <a class="row icons" style="gap: 10px;" href="https://wa.me/00963989202089" target="_blank">
                    <p>+963 989 202 089</p>
                    <b-icon icon="whatsapp"></b-icon>
                </a>
                | <a class="row icons" style="gap: 10px;" href="tel:+963989202090">
                    <p>+963 989 202 090</p>
                    <b-icon class="icon-contact" icon="telephone-fill" aria-hidden="true"></b-icon>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ContcatBar",
    data() {
        return {
            lang: '',
            screenWidth: 0,
            display_lang: '',
        };
    },
    computed: {
        isAr() {
            this.lang = localStorage.getItem("lang")
            return this.lang == 'ar'
        },
    },
    methods: {
        onScreenResize() {
            window.addEventListener("resize", () => {
                this.updateScreenWidth();
            });
        },
        updateScreenWidth() {
            this.screenWidth = window.innerWidth;
        },
        handleChange(selectedLang) {
            localStorage.setItem('lang', selectedLang)
            window.location.reload()
            console.log(selectedLang)
        },
    },
    mounted() {
        this.lang = localStorage.getItem("lang")
        if (this.lang == "ar")
            this.display_lang = "العربية"
        else
            this.display_lang = "English"
        this.updateScreenWidth();
        this.onScreenResize();
    },
};

</script>


<style lang="scss">
.contact-bar {
    color: #fff;
    padding: 20px 95px 0px 135px;
    border-bottom: 0.1px solid #ffffff7a;
    z-index: 10000;
    position: relative;
}

@media (max-width: 991px) {
    .contact-bar {
        background: rgba(255, 255, 255, 0.968627451);
        padding: 15px 0px 0px 45px;
    }

    .contact-bar .icons {
        color: var(--main-blue) !important;
        margin-right: 0px;
    }

    .my-navbar .navbar .collapsed {
        padding: 0px;
    }
}

.contact-bar .right-side {
    gap: 50px;
}

.contact-bar .bi-globe {
    font-size: 17px !important;
}

.contact-bar .b-icon {
    margin-top: 5px;
    cursor: pointer;
    margin-bottom: 2px;
}

.contact-bar .social-icon:hover {
    cursor: pointer;
    font-size: 19px;
    transition: all .3s;
}

.contact-bar .social-icon {
    transition: all .3s;
}

.contact-bar .icons {
    color: #fff;
}

.contact-bar .bi-chevron-down {
    font-size: 15px !important;
}

.contact-bar .dropdown-item.active,
.dropdown-item:active {
    color: #fff;
    text-decoration: none;
    background-color: var(--main-blue) !important;
}

.contact-bar .dropdown {
    top: -10px;
    right: 40px;
}
</style>
