<script>
import Navbar from "@/components/Navbar.vue"
import CustomFooter from "@/components/CustomFooter.vue"

export default {
    name: "Portfolio",
    components: {
        Navbar,
        CustomFooter,
    },
    data() {
        return {
            data: [],
            load: false,
        };
    },
    methods: {
        getPortfolio() {
            this.lang = localStorage.getItem("lang");
            this.axios.get("https://peaklinksite.peaklinkdemo.com/api/get_project?language=" + this.lang)
                .then(res => {
                    if (res.data.error == 'false') {
                        this.data = res.data.data
                        this.load = true
                    }
                    console.log(this.data)
                });
        },
    },

    mounted() {
        document.documentElement.scrollTop = 0;
    },
    created(){
        this.getPortfolio()
    }
};
</script>

