<template>
    <section id="product-form" class="product-form">
        <div class="container">
            <b-form>
                <div class="container-form">
                    <h3 class="tap-name">
                        Personal Info
                    </h3>
                    <div class="line"></div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="name" label="Full Name" label-for="name">
                                <b-form-input id="name" v-model="name" placeholder="Full Name"></b-form-input>
                                <div class="error-section" v-if="v$.name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="email" label="Email" label-for="email">
                                <b-form-input id="email" v-model="email" placeholder="Your Email"></b-form-input>
                                <div class="error-section" v-if="v$.email.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.email.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="phone" label="Phone Number" label-for="phone">
                                <b-form-input id="phone" v-model="phone" placeholder="Phone Number"></b-form-input>
                                <div class="error-section" v-if="v$.phone.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.phone.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>

                    </div>

                    <h3 class="tap-name" style="margin-top: 40px">
                        Project Info
                    </h3>
                    <div class="line"></div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="project_requirements" label="Project Requirements"
                                label-for="project_requirements">
                                <b-form-input id="project_requirements" v-model="project_requirements"
                                    placeholder="Project Requirements"></b-form-input>
                                <div class="error-section" v-if="v$.project_requirements.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.project_requirements.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="file" label="Project Details" label-for="file">
                                <b-form-file @change="onFileSelected" v-model="file"></b-form-file>
                                <div class="error-section" v-if="v$.file.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.file.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group label="Project Type" label-for="project_type">
                                <b-form-checkbox-group v-model="project_type"
                                    :options="project_type_option"></b-form-checkbox-group>
                                <div class="error-section" v-if="v$.project_type.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.project_type.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <!-- <div class="col-lg-6">
                            <b-form-group label="Language " label-for="language">
                                <b-form-radio-group v-model="language" :options="language_option"></b-form-radio-group>
                                <div class="error-section" v-if="v$.language.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.language.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div> -->
                    </div>
                    <div class="line"></div>
                    <div style="text-align:justify">
                        <button v-on:click="submitForm" type="button" class="my-button"> Submit</button>
                    </div>
                </div>
            </b-form>
        </div>
    </section>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, email, helpers, minLength, url } from '@vuelidate/validators'


export default {
    name: "ProductForm",
    setup() {
        return { v$: useVuelidate() }
    },
    components: {

    },
    data() {
        return {
            name: '',
            email: '',
            phone: '',
            company_name: '',
            company_site: '',
            project_name: '',
            project_requirements: '',
            project_type: [],
            project_type_option: ['Web', 'Mobile', 'Graphic Design'],
            project_details: '',
            expected_budget: null,
            date: '',
            language: '',
            language_option: ['Multi', 'Single'],
            file: null,
        };
    },
    validations() {
        return {
            name: {
                required: helpers.withMessage('This field is required', required)
            },
            email: {
                required: helpers.withMessage('This field is required', required),
                email: helpers.withMessage('Please enter valid email', email)
            },
            phone: {
                required: helpers.withMessage('This field is required', required),
                minLength: helpers.withMessage('Please enter valid phone number', minLength(10))
            },
            company_name: {
                required: helpers.withMessage('This field is required', required)
            },
            company_site: {
                required: helpers.withMessage('This field is required', required),
                url: helpers.withMessage('Please enter valid url', url)
            },
            project_name: {
                required: helpers.withMessage('This field is required', required)
            },
            project_requirements: {
                required: helpers.withMessage('This field is required', required)
            },
            project_type: {
                required: helpers.withMessage('Please select project type', required)
            },
            project_details: {
                required: helpers.withMessage('This field is required', required)
            },
            expected_budget: {
                required: helpers.withMessage('This field is required', required)
            },
            date: {
                required: helpers.withMessage('This field is required', required)
            },
            language: {
                required: helpers.withMessage('Please select language', required)
            },
            file: {
                required: helpers.withMessage('This field is required', required)
            },
        }
    },
    methods: {
        submitForm() {
            // console.log("Sssssssss");
            this.v$.$validate();
            if (!this.v$.$error) {
                console.log("TRRRRRRUUUUEEEEE")
            }
            // console.log(this.v$.$error)
        },
        onFileSelected(files) {
            this.file = files.target.files[0];
        },
    }
};
</script>

<style lang="scss">
.product-form {
    padding: 50px
}

.product-form .form-group {
    justify-content: space-between;
    margin: 20px 0px;
}

.product-form .container-form {
    margin-right: 4%;
    margin-left: 4%;
    margin-top: 30px;
}

.product-form .form-control,
.product-form .custom-file {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 10px;
    background-color: #bebebe11;
    transition: all 0.5s ease;
}

.product-form .form-control:focus {
    box-shadow: none;
    border: 1px solid var(--main-blue);
    background: #fff;
    transition: all 0.5s ease;
    box-shadow: 0px 0px 6px #24487961;
}

.product-form .d-block {
    color: var(--main-blue);
    text-align: justify;
    font-size: 18px;
}

.product-form .line {
    border-bottom: 1px solid #bebebe;
    margin-top: 20px;
}

.product-form .tap-name {
    text-align: justify;
    color: var(--main-blue);
    font-weight: 800;
}

@media (min-width: 992px) {
    .product-form .my-row {
        margin-top: 20px;
    }
}

.product-form .error {
    color: #b61532;
    margin: 5px;
}

.product-form .bi-exclamation-triangle {
    color: #b61532;
    font-size: 18px;
}

.product-form .error-section {
    margin-top: 10px;
    text-align: justify;
}


//button
.product-form .my-button {
    position: relative;
    color: var(--main-blue);
    font-size: 16px;
    font-weight: 800;
    text-decoration: none;
    margin: 30px 0;
    border: 1px solid var(--main-blue);
    padding: 12px 30px;
    overflow: hidden;
    transition: .6s all ease;
    background: transparent;
    border-radius: 10px;
    z-index: 100000;
}

.product-form .my-button::before {
    background: var(--main-blue);
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    transition: all .6s ease;
}

.product-form .my-button:hover {
    color: #fff;
}

.product-form .my-button::before {
    width: 0%;
    height: 101%;
}

.product-form .my-button:hover::before {
    width: 100%;
}

////
.product-form .bv-no-focus-ring {
    text-align: justify;
    margin-top: 30px;
}

.product-form .custom-control-label::before {
    width: 25px;
    height: 25px;
    margin-left: -15px;
    margin-top: -9px;
}

.product-form .custom-control-label {
    margin-left: 15px;
    cursor: pointer
}

.product-form .custom-control-label::after {
    top: -6px;
    left: -40px;
    width: 26px;
    height: 26px;
}

.product-form .custom-control-input:checked~.custom-control-label::before {
    color: #fff;
    border-color: var(--main-blue);
    background-color: var(--main-blue);
    box-shadow: 0px 0px 6px #24487961;
}

//date

.product-form .b-form-btn-label-control.form-control {
    height: 50px;
}

.product-form .b-form-btn-label-control.form-control>.form-control {
    text-align: justify;
    margin-top: 5px
}

.product-form .bi-calendar,
.product-form .bi-calendar-fill {
    color: var(--main-blue)
}

.btn-outline-primary,
.btn-outline-primary:hover,
.btn-outline-primary:active,
.btn-outline-primary:visited,
.btn-outline-primary:focus {
    background-color: var(--main-blue) !important;
    border-color: var(--main-blue) !important;
}

.btn-outline-primary {
    color: #fff !important;
}

.btn-outline-primary:focus,
.btn-outline-primary.focus {
    box-shadow: 0 0 0 0.6px #24487961 !important;
}

.form-control.focus {
    border: 1px solid var(--main-blue) !important;
    box-shadow: 0px 0px 6px #24487961 !important;
}

.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show>.btn-primary.dropdown-toggle {
    background-color: var(--main-blue) !important;
    box-shadow: 0px 0px 6px #24487961 !important;
}

.b-calendar-grid-help {
    display: none !important;
}

//file
.custom-file-label {
    height: 50px !important;
    background-color: #bebebe11 !important;
    border: none !important;
    cursor: pointer;
}

.form-file-text {
    color: #6c757d !important;
    font-size: 16px !important;
    margin-top: 5px;
}

.b-form-file {
    margin-top: 0px !important
}

.custom-file-input~.custom-file-label[data-browse]::after {
    height: 50px;
}

.custom-file-label::after {
    line-height: 35px !important;
}
</style>