<template>
  <div id="app" :class="[isAr ? 'ar' : 'en']">
    <router-view />
  </div>
</template>

<script>
export default {
  data() {
    return {
      lang: '',
    }
  },
  computed: {
    isAr() {
      this.lang = localStorage.getItem("lang")
      let htmlElement = document.documentElement
      if (this.lang == 'ar') {
        htmlElement.setAttribute('lang', 'ar')
        return true
      }
      else {
        htmlElement.setAttribute('lang', 'en')
        return false
      }
    }
  },
  created() {
    this.lang = localStorage.getItem("lang")
    if (this.lang == null)
      localStorage.setItem("lang", "en")
  }
};
</script>

