import Vue from 'vue'
import VueRouter from 'vue-router'
import Landing from '@/views/Landing.vue'
import GetProductForm from "@/views/GetProductForm.vue"
import GetJobForm from "@/views/GetJobForm.vue"
import OrderProjectForm from "@/views/OrderProductForm.vue"
import Portfolio from "@/views/Portfolio.vue"
import AboutPage from "@/views/AboutPage.vue"
import Project from "@/views/Project.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'landing',
    component: Landing
  },
  {
    path: '/get-product',
    name: 'get-product',
    component: GetProductForm
  },
  {
    path: '/get-job',
    name: 'get-job',
    component: GetJobForm
  },
  {
    path: '/order-project',
    name: 'order-project',
    component: OrderProjectForm
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: Portfolio
  },
  {
    path: '/about',
    name: 'about',
    component: AboutPage
  },
  {
    path: '/project',
    name: 'project',
    component: Project
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
