<template>
    <section id="project-form" class="project-form">
        <div class="container">
            <b-form>
                <div class="container-form">
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="name" label="Full Name" label-for="name">
                                <b-form-input id="name" v-model="name" placeholder="Full Name"></b-form-input>
                                <div class="error-section" v-if="v$.name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="phone" label="Phone Number" label-for="phone">
                                <b-form-input id="phone" v-model="phone" placeholder="Phone Number"></b-form-input>
                                <div class="error-section" v-if="v$.phone.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.phone.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>

                    </div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="company_name" label="Company Name" label-for="company_name">
                                <b-form-input id="company_name" v-model="company_name"
                                    placeholder="Company Name"></b-form-input>
                                <div class="error-section" v-if="v$.company_name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.company_name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="project_name" label="Project Name" label-for="project_name">
                                <!-- <b-form-input id="project_name" v-model="project_name"
                                    placeholder="Project Name"></b-form-input> -->
                                <select v-model="project_name" class="my-select">
                                    <option disabled value="" class="my-option">Please select one</option>
                                    <option v-for="(item, index) in project_name_option" :key="index">
                                        {{ item }}
                                    </option>
                                </select>
                                <div class="error-section" v-if="v$.project_name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.project_name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div class="row my-row">
                        <div class="col-lg-12">
                            <b-form-group id="message" label="Message" label-for="message">
                                <b-form-textarea id="message" v-model="message" placeholder="Tell us about your project "
                                    style="height:120px"></b-form-textarea>
                                <div class="error-section" v-if="v$.message.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.message.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div style="text-align:justify">
                        <button v-on:click="submitForm" type="button" class="my-button"> Submit</button>
                    </div>
                </div>
            </b-form>
        </div>
    </section>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, helpers, minLength } from '@vuelidate/validators'

export default {
    name: "ProjectForm",
    setup() {
        return { v$: useVuelidate() }
    },
    components: {

    },
    data() {
        return {
            name: '',
            phone: '',
            company_name: '',
            project_name: '',
            project_name_option: ['Peak Link Academy', 'Peak Link Clinic', 'Peak Link ERP', 'Peak Link HR'],
            message: '',
        };
    },
    validations() {
        return {
            name: {
                required: helpers.withMessage('This field is required', required)
            },
            phone: {
                required: helpers.withMessage('This field is required', required),
                minLength: helpers.withMessage('Please enter valid phone number', minLength(10))
            },
            company_name: {
                required: helpers.withMessage('This field is required', required)
            },
            project_name: {
                required: helpers.withMessage('This field is required', required)
            },
            message: {
                required: helpers.withMessage('This field is required', required)
            }

        }
    },
    methods: {
        submitForm() {
            this.v$.$validate();
            if (!this.v$.$error) {
                console.log("TRRRRRRUUUUEEEEE")
            }
        },
    }
};
</script>

<style lang="scss">
.project-form {
    padding: 50px
}

.project-form .form-group {
    justify-content: space-between;
    margin: 20px 0px;
}

.project-form .container-form {
    margin-right: 4%;
    margin-left: 4%;
    margin-top: 30px;
}

.project-form .form-control,
.project-form .custom-file {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 10px;
    background-color: #bebebe11;
    transition: all 0.5s ease;
}

.project-form .form-control:focus {
    box-shadow: none;
    border: 1px solid var(--main-blue);
    background: #fff;
    transition: all 0.5s ease;
    box-shadow: 0px 0px 6px #24487961;
}

.project-form .d-block {
    color: var(--main-blue);
    text-align: justify;
    font-size: 18px;
}



@media (min-width: 992px) {
    .project-form .my-row {
        margin-top: 20px;
    }
}

.project-form .error {
    color: #b61532;
    margin: 5px;
}

.project-form .bi-exclamation-triangle {
    color: #b61532;
    font-size: 18px;
}

.project-form .error-section {
    margin-top: 10px;
    text-align: justify;
}


//button
.project-form .my-button {
    position: relative;
    color: var(--main-blue);
    font-size: 16px;
    font-weight: 800;
    text-decoration: none;
    margin: 30px 0;
    border: 1px solid var(--main-blue);
    padding: 12px 30px;
    overflow: hidden;
    transition: .6s all ease;
    background: transparent;
    border-radius: 10px;
    z-index: 100000;
}

.project-form .my-button::before {
    background: var(--main-blue);
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    transition: all .6s ease;
}

.project-form .my-button:hover {
    color: #fff;
}

.project-form .my-button::before {
    width: 0%;
    height: 101%;
}

.project-form .my-button:hover::before {
    width: 100%;
}

.project-form .my-select {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 2px;
    background-color: rgba(190, 190, 190, 0.0666666667);
    transition: all 0.5s ease;
    width: 100%;
    padding: 0px 10px;
    color: #495057;
}
</style>