                                <b-form-file @change="onCvSelected" v-model="cv"></b-form-file>
                                <div class="error-section" v-if="v$.cv.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.cv.$errors[0].$message }}</span>
