<script>
import useVuelidate from '@vuelidate/core'
import { required, helpers, minLength, email } from '@vuelidate/validators'
import ModalDetailsPosition from './ModalDetailsPosition.vue';
export default {
    name: "job_form",
    setup() {
        return { v$: useVuelidate() }
    },
    components: {
        ModalDetailsPosition
    },
    data() {
        return {
            name: '',
            email: '',
            phone_number: '',
            cv: null,
            position: null,
            modalShow: false,
            response: false,
            submit: false,
        };
    },
    validations() {
        return {
            name: {
                required: helpers.withMessage('This field is required', required)
            },
            email: {
                required: helpers.withMessage('This field is required', required),
                email: helpers.withMessage('Please enter valid email', email)
            },
            phone_number: {
                required: helpers.withMessage('This field is required', required),
                minLength: helpers.withMessage('Please enter valid phone number', minLength(10))
            },
            cv: {
                required: helpers.withMessage('This field is required', required),
            },
        }
    },
    methods: {
        submitForm() {
            this.v$.$validate();
            if (!this.v$.$error) {
                this.submit = true
                console.log("TRRRRRRUUUUEEEEE")
                this.sendData()
            }
        },
        onCvSelected(files) {
            this.cv = files.target.files[0];
        },
        setPosition(pos) {
            this.position = pos;
        },
        sendData() {
            const formData = new FormData()
            formData.append('email', this.email)
            formData.append('name', this.name)
            formData.append('cv', this.cv)
            formData.append('phone', this.phone_number)
            formData.append('position', this.position)
            console.log(formData)
            for (var pair of formData.entries()) {
                console.log(pair)
            }
            this.axios.post("https://peaklinksite.peaklinkdemo.com/api/job_form", formData)
                .then((res) => {
                    console.log(res)
                    this.response = true
                    if (res.status == 200)
                        this.modalShow = true
                })
        }
    }
};
</script>

