<template>
  <div class="landing" v-if="dataFetched()">
    <home />
    <about />
    <service />
    <product-type />
    <product />
    <order-project />
    <process />
    <partner />
    <technologies />
    <get-product />
    <client />
    <custom-footer />
  </div>
    <!-- <div class="animation">
      <img src="../assets/img/logo-color.webp" style="width:20%" />
    </div> -->


    <!-- <b-spinner v-else class="spinner" style="width: 4rem; height: 4rem;color:var(--main-blue);margin-top: 22%;"
      label="Spinning"></b-spinner> -->


      <loader3 v-else />
</template>

<script>
import Home from "@/components/Home.vue"
import About from "@/components/About.vue"
import Service from "@/components/Service.vue"
import ProductType from "@/components/ProductType.vue"
import Partner from "@/components/Partner.vue"
import GetProduct from "@/components/GetProduct.vue"
import CustomFooter from "@/components/CustomFooter.vue"
import Product from "@/components/Product.vue"
import OrderProject from "@/components/OrderProduct.vue"
import Client from "@/components/Client.vue"
import Loader3 from "@/components/Loader3.vue"
import Process from "@/components/Process.vue"
import Technologies from "@/components/Technologies.vue"
export default {
  name: "Landing",
  components: {
    Home,
    About,
    Service,
    ProductType,
    Partner,
    GetProduct,
    CustomFooter,
    Product,
    OrderProject,
    Client,
    Loader3,
    Process,
    Technologies
  },
  data() {
    return {
      scroll: '',
    };
  },
  methods: {
    dataFetched() {
      if (this.$store.state.partner_data.length != 0 &&
        this.$store.state.service_data.length != 0 &&
        this.$store.state.about_section_data != 0 &&
        this.$store.state.client_data != 0)
        return true
    },
    getService() {
      this.axios.get("https://peaklinksite.peaklinkdemo.com/api/get_service?language=" + this.lang)
        .then(res => {
          if (res.data.error == 'false')
            this.$store.state.service_data = res.data.data
        });
    },
    getPartner() {
      this.axios.get("https://peaklinksite.peaklinkdemo.com/api/get_partner")
        .then(res => {
          if (res.data.error == 'false')
            this.$store.state.partner_data = res.data.data
        });
    },
    getAbout() {
      this.axios.get("https://peaklinksite.peaklinkdemo.com/api/get_about?language=" + this.lang)
        .then(res => {
          if (res.data.error == 'false')
            this.$store.state.about_section_data = res.data.data
        });
    },
    getClient() {
      this.axios.get("https://peaklinksite.peaklinkdemo.com/api/get_client")
        .then(res => {
          if (res.data.error == 'false')
            this.$store.state.client_data = res.data.data
        });
    }
  },
  mounted() {
    this.lang = localStorage.getItem("lang");
    this.getService()
    this.getPartner()
    this.getAbout()
    this.getClient()
    this.scroll = localStorage.getItem("scroll")
    this.$scrollTo("#" + this.scroll)
  },
};
</script>

<style lang="scss">
.landing {
  height: 1000px;
}

@keyframes up-and-down {
  from {
    // opacity: 0;
    // transform: scale(70%);
    transform: translateY(50px);
  }

  to {
    // opacity: 1;
    // transform: scale(80%);
    transform: translateX(0px);
  }
}

.animation {
  animation-name: up-and-down;
  animation-duration: 0.7s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: absolute;
  top: 50%;
}
</style>
